.scrollButton {
    position: fixed;
    bottom: 100px;
    right: 20px;
    padding: 6px 12px 8px 12px;
    background-color: var(--additional-color);
    font-weight: var(--thk-font-weight);
    color: var(--inverse-text-color);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    z-index: 1000;
    transition: 0.3s ease-in-out;
}

.scrollButtonHide {
    position: fixed;
    bottom: 100px;
    right: -40px;
    padding: 6px 12px 8px 12px;
    background-color: var(--additional-color);
    font-weight: var(--thk-font-weight);
    color: var(--inverse-text-color);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    z-index: 1000;
    transition: 0.3s ease-in-out;
}