/* Messenger.module.css*/

.mainWindow{
}

.container{
}

.header {
    height: 32px;
    padding: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: var(--std-borders);
    position: sticky;
    top: 0;
    background-color: var(--background-color);
    z-index: 10;
}

.burgerMenu {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 24px;
    height: 20px;
    cursor: pointer;
}

.burgerLine {
    width: 100%;
    height: 2px;
    background-color: var(--additional-color);
    border-radius: var(--std-border-radius);

}

.connectionStatus {
    font-size: var(--sm-font-size);
    color: var(--additional-tеxt-color);
    text-align: left;
    user-select: none;
    width: 10%;
}

.infoContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.userFullName {
    font-size: var(--std-font-size);
    font-weight: var(--thk-font-weight);
}

.chatId {
    color: var(--additional-tеxt-color);
    font-size: var(--sm-font-size);
}


.content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow-y: auto;
    height: calc((90vh - 4vh) - 2vh); /* Высота контента минус высота header и MessageInput */
}

.messages{
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding:0 4px 0 4px;
    background-image:
            var(--chat-background-linare),
            var(--chat-background-image);
    background-size: 65vh;
    background-repeat: repeat, repeat;
    background-blend-mode: multiply;

}

.messages::-webkit-scrollbar {
    width: 0.2vw;
}

.messages::-webkit-scrollbar-track {
    background-color: transparent;
}


.messages::-webkit-scrollbar-thumb {
    background-color: var(--scrollbar-thumb-color);
    border-radius: var(--std-border-radius);
}

.messages::-webkit-scrollbar-thumb:hover {
    background: var(--scrollbar-thumb-hover);
}

.dateSeparator {
    text-align: center;
    margin: 10px 0;
    color: var(--inverse-text-color);
    font-size: var(--sm-font-size);
    position: relative;
    user-select: none;
}

.dateSeparatorText {
    background-color: var(--date-separator-bg);
    border-radius: var(--std-border-radius);
    padding: 5px 10px;
    display: inline-block;
}

