/* Chat.module.css */

.connectingContainer {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.connecting {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    color: #888;
    margin-top: 20px;
}

.connectingDot {
    animation: blink 1.4s infinite both;
}

.connectingDot:nth-child(2) {
    animation-delay: 0.2s;
}

.connectingDot:nth-child(3) {
    animation-delay: 0.4s;
}

.serverIcon {
    width: 40px;
    height: 40px;
    background-color: #ccc;
    border-radius: 50%;
    position: relative;
    animation: pulse 1.5s infinite;
}

.serverIcon::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 20px;
    background-color: #aaa;
    border-radius: 50%;
    transform: translate(-50%, -50%);
}

.connectingArrows {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.connectingArrow {
    width: 20px;
    height: 10px;
    background-color: #888;
    margin: 2px 0;
    animation: blink 1.4s infinite both;
}

.connectingArrow:nth-child(2) {
    animation-delay: 0.2s;
}

.connectingArrow:nth-child(3) {
    animation-delay: 0.4s;
}

@keyframes pulse {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes blink {
    0% {
        opacity: 0.2;
    }
    20% {
        opacity: 1;
    }
    100% {
        opacity: 0.2;
    }
}

.chatContainer {
    display: flex;
    overflow: hidden;
    color: var(--text-color);
    width: 100%;
    height: 100%;
}

/* Стили для списка пользователей */
.userList {
    width: 100%;
    height: 100%;
    background-color: var(--background-color);
    overflow-y: auto;
    transition: transform 0.2s ease;
    transform: translateX(-100%);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
}

.userList.visible {
    transform: translateX(0);
}

.userList h2 {
    margin: 0 0 10px;
    font-size: 18px;
}

.userList ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.userList li {
    padding: 10px;
    cursor: pointer;
    border-bottom: var(--std-borders);
}

.userList li:hover {
    background-color: var(--background-color);
}

/* Стили для чата */
.chat {
    border-radius: var(--std-border-radius);
    display: flex;
    flex-direction: column;
    background-color: var(--background-color);
    justify-content: space-between;
    width: 100%;
    height: 100%;
}