.userCard {
    display: flex;
    padding: 16px;
    background-color: var(--background-color);
    align-items: center;
    justify-content: space-between;

}
.userCard:hover {
    background-color: var(--additional-background-color);
}

.userInfoContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.userIconContainer{
    margin-right: 16px;
    width: 48px;
    height: 48px;
    padding: 8px;
    border-radius: 50%;
    background-color: var(--additional-background-color)
}

.userIcon{
    fill: var(--additional-color);
    width: 42px;
    height: 42px;
}


.userInfo {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.userFullName {
    font-weight: var(--thk-font-weight);
}

.additionInfo{
    color: var(--additional-tеxt-color)
}

.unreadCount{
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    color: var(--inverse-text-color);
    background-color: var(--additional-color);
    min-width: 24px;
    height: 24px;
    padding: 2px;
}

.count{
    padding-top: 1px;
    height: 21px;
}
