.message {
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 10px;
    max-width: 70%;
    word-wrap: break-word;
    display: flex;
    flex-direction: column;
}

.leftMessage {
    color: var(--text-color);
    background-color: var(--message-left-color);
    align-self: flex-start;
    justify-self: flex-end;
}

.rightMessage {
    color: var(--text-color);
    background-color: var(--message-right-color);
    align-self: flex-end;
    justify-self: flex-end;
}

.fileAttachment {
    display: flex;
    flex-direction: column;
}

.file{
    width: 150px;
    height: 150px;
    border-radius: var(--std-border-radius);
    background-color: var(--additional-background-color);
    margin-bottom: 10px;
}

.fileName {
    color: var(--additional-tеxt-color);
    font-size: var(--std-font-size);
    max-width: 150px;
}

.image {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: var(--std-border-radius);

}

.video {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: var(--std-border-radius);
}

.document {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 150px;
    height: 150px;
}

.fileSize {
    color: var(--additional-tеxt-color);
    font-size: var(--sm-font-size);
}

.messageText {
    text-align: left;
}

.messageInfo{
    display: flex;
    padding-top: 2px;
    justify-content: flex-end;
}

.statusError {
    margin-left: 6px;
    text-align: right;
    font-size: var(--sm-font-size);
    user-select: none;
    color: var(--color-red);
    width: 100%;
}

.statusSent {
    margin-left: 6px;
    text-align: right;
    font-size: var(--sm-font-size);
    user-select: none;
    color: var(--color-green);
    width: 100%;
}

.statusSending {
    margin-left: 6px;
    text-align: right;
    font-size: var(--sm-font-size);
    user-select: none;
    color: var(--additional-tеxt-color);
    width: 100%;
}


.dateTime {
    font-size: var(--sm-font-size);
    color: var(--additional-tеxt-color);
}

.shimmerSquare {
    width: 150px;
    height: 150px;
    border-radius: var(--std-border-radius);
    background: linear-gradient(90deg, var(--background-color) 25%, var(--additional-background-color) 50%, var(--background-color) 75%);
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite;
    margin-bottom: 10px;
}

@keyframes shimmer {
    0% {
        background-position: -200% 0;
    }
    100% {
        background-position: 200% 0;
    }
}