/* ThemeToggle.module.css */
.toggleContainer {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 32px;
}

.toggleContainer input {
    opacity: 0;
    width: 0;
    height: 0;
}

.toggleSlider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--additional-background-color);
    transition: 0.4s;
    border-radius: 34px;
}

.toggleSlider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 3px;
    background-color: var(--background-color);
    transition: 0.4s;
    border-radius: 50%;
}

input:checked + .toggleSlider {
    background-color: var(--additional-color);
}

input:checked + .toggleSlider:before {
    transform: translateX(26px);
}