.container {
    width: 100%;
    background-color: var(--background-color);
}

.searchContainer {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 28px;
    background-color: var(--background-color)
}

.search{
    width: 50%;
    height: 24px;
    font-size: var(--std-font-size);
    padding: 4px;
    background-color: var(--additional-background-color);
    border-radius: var(--std-border-radius);
    color: var(--text-color);
}