.notification {
    display: flex;
    background-color: var(--background-color);
    padding: 10px;
    border-radius: var(--std-border-radius);
    box-shadow: 10px 10px 20px var(--additional-background-color);
    width: 80vw;
    justify-content: space-between;
    align-items: center;
    font-size: var(--std-font-size);
}

.notificationContent {
    margin-top: 6px;
    display: flex;
    flex-direction: column;
    color: var(--text-color);
    justify-content: flex-start;
    text-align: left;
    max-width: 70%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

}

.notificationUser {
    color: var(--text-color);
    font-weight: var(--thk-font-weight);
    margin-bottom: 4px;
}
.notificationText {

}

.notificationClose {
    width: 30%;
    color: var(--color-green);
}

