* {
  /* reboot style */
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
}
@viewport {
  user-zoom: fixed;
}
::-webkit-scrollbar { width: 0;}
::-webkit-scrollbar { width: 0 !important }

:root {
  -ms-overflow-style: none;
  /* Light Theme */
  --background-color: #fdfdfd;
  --additional-background-color: #f2f2f3;
  --app-background: #6f7478;
  --text-color: #000000;
  --additional-tеxt-color: #6f7478;
  --inverse-text-color: #ffffff;

  --lg-font-size: 18px;
  --std-font-size: 16px;
  --sm-font-size: 14px;

  --thn-font-weight: 300;
  --std-font-weight: 400;
  --thk-font-weight: 500;

  --std-border-radius: 10px;
  --std-borders: 1px solid #ccc;

  /* Chat Theme */
  --chat-background-image: url('/static/chat-bg-pattern-light.png');
  --chat-background-linare: linear-gradient(to bottom, rgb(205, 215, 179) 0%, rgb(138, 186, 132) 100%);

  --date-separator-bg: rgba(0, 0, 0, .2);

  --scrollbar-thumb-color: #a6a7ab;
  --scrollbar-thumb-hover: #888888;

  --additional-color: #338feb;

  --message-right-color: #dcf8c6;
  --message-left-color: #f1f1f1;

  --input-button-color: #000000;

  --color-red: #ff4500ff;
  --color-green: #008000ff;
}

[data-theme="dark"] {
  -ms-overflow-style: none;
  /* Light Theme */
  --background-color: #212121;
  --additional-background-color: #181818;
  --app-background: #181818;
  --text-color: #ffffff;
  --additional-tеxt-color: #6f7478;
  --inverse-text-color: #000000;

  --std-borders: 1px solid #2f2f2f;

  /* Chat Theme */
  --chat-background-image: url('/static/chat-bg-pattern-dark.png');
  --chat-background-linare: linear-gradient(to bottom, rgba(0, 0, 0, 0.38) 0%, rgba(0, 0, 0, 0.38) 100%);

  --date-separator-bg: rgba(134, 115, 224, 0.5);

  --scrollbar-thumb-color: #a6a7ab;
  --scrollbar-thumb-hover: #888888;

  --additional-color: #8673e0;

  --message-right-color: #212121;
  --message-left-color: #212121;

  --input-button-color: #8673e0;
}


body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin-top: 4vh;
  height: 96vh;
  padding: 0;
  position:fixed;
  width: 100%; /* Фиксирует ширину страницы */
}

.notistack-SnackbarContainer {
  top: -10px !important;
  display: flex !important;
  justify-content: flex-start !important;
  align-items: flex-start !important;
  pointer-events: none !important;
}

.notistack-Snackbar {
  pointer-events: auto !important;
}