.inputContainer {
    height: fit-content;
    position: sticky;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    bottom: 0;
    background-color: var(--background-color);
    border-top: var(--std-borders);
    z-index: 10;
}



.AttachButton{
    height: 48px;
}

.attachedFiles{
    position: absolute; /* Добавляем абсолютное позиционирование */
    bottom: 100%; /* Помещаем контейнер над inputContainer */
    width: 100%;
    background-color: var(--background-color);
    border-bottom: var(--std-borders);
}

.attachedFileContainer {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--background-color);
}
.attachedFileCounterWeight{
    width: 14px;
    padding-left: 8px;
}

.attachedFile{
    color: var(--text-color);
    max-width: 50%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.attachedFileCancel {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 14px;
    height: 14px;
    cursor: pointer;
    padding-right: 8px;
}

.attachedFileCancelLine {
    position: absolute;
    width: 14px;
    height: 2px;
    background-color: var(--color-red);
}

.attachedFileCancelLine:nth-child(1) {
    transform: rotate(45deg);
}

.attachedFileCancelLine:nth-child(2) {
    transform: rotate(-45deg);
}

.fileInput {

}

.TextArea {
    width: 100%;
    height: 28px;
    text-align: left;
    line-height: 1.1;
    resize: none;
    border: none;
    overflow-y: hidden;
    outline: none;
    color: var(--text-color);
    font-size: var(--std-font-size);
    background-color: var(--background-color);
    display: flex;
}
.TextArea::placeholder {
    align-items: center;
}
.TextArea::-moz-placeholder {
    vertical-align: middle;
}

.sendButtonContainer{
    height: 48px;
}

